import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController,
  ModalController,
} from "ionic-angular";
import { MaskColumnViewModel } from "../../Model/ViewModel/MaskColumnViewModel";

/**
 * Generated class for the MaskColumnModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page mask-column-add-edit-modal",
  templateUrl: "mask-column-add-edit-modal.html"
})
export class MaskColumnAddEditModalPage {
  item: MaskColumnViewModel;
  mode: string;
  title: string;
  CanEditBatch: boolean;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public modalCtrl: ModalController,

  ) {
    this.item = new MaskColumnViewModel();
    this.item.schemaname = navParams.data.item.schemaname;//結構描述
    this.item.tablename = navParams.data.item.tablename;//資料表
    this.item.columnname = navParams.data.item.columnname;//欄位名稱
    this.item.col_start_loc = navParams.data.item.col_start_loc;//資料欄起始位置
    this.item.col_length = navParams.data.item.col_length;//資料欄位長度
    this.item.mask = navParams.data.item.mask;//遮罩字元
    this.item.creator = navParams.data.item.creator;//鍵檔人員
    this.item.create_time = navParams.data.item.create_time;//建立時間
    this.item.modifier = navParams.data.item.modifier;//異動人員
    this.item.last_update_time = navParams.data.item.last_update_time;//最後更新時間


    this.mode = navParams.data.mode;
    this.CanEditBatch = navParams.data.CanEditBatch;
    if (this.mode === "POST") this.title = "新增";
    else this.title = "更新";
  }
  SelectColumn() {
    let modal = this.modalCtrl.create("TableColumnExtSelectModalPage", {
      item: this.item,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.columnname = select_data.columnname;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad MaskColumnAddEditModalPage");
  }
}



